*{
  margin: 0;
  padding: 0;
}
.avatar{
  width:100px;
}

/* login page */

.login{

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.main{
  display: flex;
  flex-direction: row;
  height: 70%;
  box-shadow: grey 2px 2px 25px 10px;
}
.right{
  flex: 1;
  
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 100px;

}
.login-banner{
  filter: brightness(0.8);
  object-fit: fill;
  width: 100%;
}
.left{
  flex: 1;
  contain:content;
}

.row-container  {
  display:flex;
  flex-direction: row;
}
.column{
  display: flex;
  flex-direction: column;
}
.space-bw{
  justify-content: space-between;
  width: 100%;
}

.title{
  color:rgb(51, 51, 51);
  font-size: 2em;
  font-weight: bold;
}
.icon-container{
  display: inline-block;
}
.icon{
  display: inline-block;
  padding: 10px;
}


.facebook{
  height: 100px;
}
form{
  width: 100%;
}
label{
  font-weight: 800;
  color: rgb(51, 51, 51);
}
input{
  font-size:1em;
  padding: 10px;
  border-radius: 5px;
  width: 100%;
  border: 1px solid rgb(51, 51, 51);
  margin-bottom: 10px;

}
.form-control{
  border: none;
  width: fit-content;
}
input[type=checkbox]{
width: fit-content;
margin: 5px;
}
.btn{
  width: 100%;
  background-color: #ddbc5b;
  border: none;
  color: white;
  font-weight: bold;
  margin-bottom: 10px;
}

.forgot{
  font-size: 0.75em;
  font-weight: 400;
  width: 150px;
}
.bold-color-text{
  color:#ddbc5b;
  
}



/* Signup */
.signup-container{
  height: 80%;
}



/* dashboard */
.dashboard{
  display: flex;


}
.leftNav{
  flex:0.2;
  border-right:2px solid grey;
}
.menu-ul{
  list-style-type: none;
  padding: 0;
}

.menu-ul li{
  padding: 10px;
  border-bottom:2px solid grey ;

}

.menu-ul li:hover{
  background-color: rgb(196, 193, 193);
}

.mainDash{
  flex:1;
}

.collapse span{
  margin: 10px;
}

.tiles-container{
  display: flex;
  flex-direction:row;
  justify-content: space-around;
  margin: 50px 10px;
  
}
.tiles{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border:2px solid grey;
  border-radius: 15px;
  width: 200px;

}

h3{
  color:grey
}

.chartpannel{
  padding:10px;
}

@media screen and (max-width:700px){
.icon-container{
  display: none;
}
.right{
  padding: 10px;
}
.left {
  display: none;
}

.main{
  height: 100%;

}
.leftNav{
  display:none;
}


.tiles-container{
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.tiles{
  margin: 20px;
}
.mainDash{
  overflow-x: hidden;
}
}

